.header {
  position: relative;
  width: 100%;
  height: var(--header-height);
}

.header-wrap {
  position: fixed;
  top: 0;
  left: 0;

  width: 100%;
  height: inherit;

  display: flex;
  justify-content: space-between;
  align-items: center;
  z-index: $layerFix;

  padding-left: 3.6rem;
  padding-right: 3.6rem;
}

// ----------------------------------------------------------------------------------------

.header-title {
  width: 40%;

  .title {
    font-family: 'Courier New', Courier, monospace;
    font-size: $fontSize-20;
    color: $white;
  }
}

// ----------------------------------------------------------------------------------------
// header theme
// icon
// ----------------------------------------------------------------------------------------

.header-drawer-container {
  width: 40%;
  position: relative;
  text-align: right;
}

// ----------------------------------------------------------------------------------------

// state
.header-drawer {
  .drawer-container {
    position: relative;
    visibility: hidden;
  }

  &[aria-expanded='true'] {
    .drawer-container {
      visibility: visible;
    }
  }
}

.btn-drawer-trigger {
  position: relative;
}

.drawer-wrap {
  position: absolute;
  right: 0;
  top: 22px;

  display: flex;
  flex-direction: column;
  align-items: flex-start;
  row-gap: 3.4rem;

  width: 22rem;
  padding: 2.4rem 1.7rem;
  text-align: left;

  background-color: $white102;
  border: 1px solid $white105;
  border-radius: 4px;

  backdrop-filter: blur(2px);
  color: $white;
}

// ----------------------------------------------------------------------------------------

@mixin navCategoryStyle {
  font-size: $fontSize-16;
  color: $white;
}

@mixin navBtnStyle {
  text-decoration: underline;
  font-size: $fontSize-14;
  color: $white;
  font-weight: $fontRegular;

  &[aria-disabled] {
    opacity: 0.5;
    cursor: initial;
  }
}

// ----------------------------------------------------------------------------------------

.nav-pages {
  display: flex;
  flex-direction: column;
  row-gap: 2.4rem;

  h3 {
    @include navCategoryStyle;
  }
}

.pages-list {
  padding-left: 1rem;

  display: flex;
  flex-direction: column;
  row-gap: 1.2rem;

  li {
    display: inline-flex;
  }

  .btn {
    @include navBtnStyle;
  }
}
// ----------------------------------------------------------------------------------------

.btn-language {
  @include navBtnStyle;
}

// ----------------------------------------------------------------------------------------

.more-links-wrap {
  width: 100%;

  details {
    width: 100%;

    summary {
      width: 100%;
      display: flex;
      justify-content: space-between;
      @include navCategoryStyle;
    }
  }
}

.more-links-list {
  padding-left: 1rem;

  display: flex;
  flex-direction: column;
  row-gap: 1.2rem;

  li {
    display: inline-flex;
  }

  .btn {
    @include navBtnStyle;
  }
}
